import React from "react";
import ImageCard from "./Game Cards/ImageCard";
import images from "./Game Cards/imageList";

class Counter extends React.Component {
  constructor(props) {
    super(props);
    this.imageCardElement = React.createRef();
  }

  state = { count: 0 };

  handleClick = () => {
    let currentImg = this.imageCardElement.current;

    console.log(`isClicked: ${currentImg.state.isClicked}`);

    if (currentImg.state.isClicked === false) {
      this.setState({ count: this.state.count + 1 });
      currentImg.updateImage();
    } else {
      this.setState({ count: 0 });
      console.log(this);
      // this.imageCardElement.resetImage();
    }
  };

  render() {
    return (
      <main>
        <div className="scoreboard bg-secondary">
          <div className="container py-3">
            <div className="row">
              <h2>Score: {this.state.count}</h2>
            </div>
          </div>
        </div>
        
        <div className="container py-3">
          <div className="row">
            {images.map(image => (
              // <button
              //   key={image.key}
              //   onClick={this.handleClick}
              //   className="col-lg-2 col-md-3 col-sm-6 p-2 border-0 bg-transparent"
              // >
              <ImageCard
                key={image.key}
                ref={this.imageCardElement}
                click={this.handleClick}
                src={image.src}
                alt={image.alt}
              />
              // </button>
            ))}
          </div>
        </div>
      </main>
    );
  }
}

export default Counter;
